html{box-sizing:border-box}*,*:before,*:after{box-sizing:inherit}.c-greedy-nav{display:flex;justify-content:space-between;max-width:980px;padding-top:2px;width:100vw}.c-greedy-nav__btn{appearance:none;background-color:transparent;border:0;outline:0;white-space:nowrap}.c-greedy-nav__bullet{font-size:inherit}.c-greedy-nav__bullet:nth-of-type(1n+2){margin-left:2px}.c-greedy-nav__fixed{display:flex}.c-greedy-nav__greedy{display:flex}.c-greedy-nav__item{display:flex;justify-content:center}.c-greedy-nav__list{display:flex;flex:0 1 auto;list-style:none;margin:0;padding:0}.c-greedy-nav__list>.c-greedy-nav__item:nth-of-type(1n+2){margin-left:0}.c-greedy-nav__link{color:#fff;flex:1 1 auto;padding:1rem;text-align:center;text-decoration:none;transition:all .2s ease-in-out;white-space:nowrap}.c-greedy-nav__link:active,.c-greedy-nav__link:hover,.c-greedy-nav__link--selected{background:#fff;color:#4792ba}.c-greedy-nav__overflow-list{background-color:#3881aa;border:2px solid #4792ba;border-top:0;display:flex;flex:1 1 auto;flex-direction:column;margin:0;padding:0;position:absolute;right:0;top:100%}.l-greedy-nav{background:#4792ba;display:flex;justify-content:center;padding:0 5px}.l-greedy-nav__item{display:flex;position:relative}.s-greedy-nav-hidden{display:none}
